// EventModel.ts
import { z } from "zod";

//   
export const createObjectModelSchema = <
  T extends z.ZodTypeAny
>(
  itemSchema: T, //   
) =>
  z.object({
    key: z.string(), //  
    content: itemSchema, //   
    count: z.string().optional(), // count  
  });

export const createListModelSchema = <
  T extends z.ZodTypeAny
>(
  itemSchema: T, //   
) =>
  z.object({
    key: z.string(), //  
    content: z.array(itemSchema), //   
    count: z.string().optional(), // count  
  });

